import { Component, OnInit, Injector, Inject, OnDestroy } from '@angular/core';
import { MainMenuOption } from '@allianzSND/ui';
import { Language, TranslateService, APIFactory, APIDispatch, CheckPermissionService, CustomRouterReuseStrategy, DataSyncService, DefaultLoginMgr, NotificationMgr, AppRouter, DeviceService, ConfigToken, APPMODE } from '@allianzSND/core';
import { RouteReuseStrategy, Router } from '@angular/router';
import { animate, style, transition, trigger, state } from '@angular/animations';
import { MenuService } from '../../service/menu.service';
import { format } from 'date-fns';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'snd-menu',
  templateUrl: './menu.component.html',
  styleUrls: ['./menu.component.scss'],
  animations: [
    trigger(
      'toggleItemCollapseAnimation', [
      state('shown', style({
        height: '*',
        paddingTop: '*',
        paddingBottom: '*',
        opacity: 1
      })),
      state('hidden', style({
        height: '0',
        paddingTop: '0',
        paddingBottom: '0',
        opacity: 0
      })),
      transition('shown => hidden', [
        style({
          height: '*',
          paddingTop: '*',
          paddingBottom: '*',
          opacity: 1
        }),
        animate('200ms ease-in', style({
          opacity: 0
        })),
        animate('200ms ease-in', style({
          height: '0',
          paddingTop: '0',
          paddingBottom: '0',
        })),

      ]),
      transition('hidden => shown', [
        style({
          height: '0',
          paddingTop: '0',
          paddingBottom: '0',
          opacity: 0
        }),
        animate('500ms ease-in', style({
          height: '*',
          paddingTop: '*',
          paddingBottom: '*',
        })),
        animate('200ms ease-in', style({
          opacity: 1
        }))
      ]),
    ]
    )
  ]
})

export class MenuComponent implements OnInit, OnDestroy {
  public language: Language = new Language();
  public leftMenu: Array<MainMenuOption> = [];
  public quickMenu: Array<MainMenuOption> = [];
  public updateTimeTitle: string = this.language.updateTime;
  public logout: string = this.language.logOut;
  public moreTxt: string = this.language.more;
  public mobileMenuTitle = this.language.mainMenu;
  public menuMoreOpen: boolean = false;
  public menuHeaderOpen: boolean = false;
  public mobileContentHeight: number = 0;
  public settingOpen = false;
  public updateTime: string = null;
  private baseRouter: Router = null;
  private unsubscribe$: Subject<void> = new Subject();

  constructor(
    private loginMgr: DefaultLoginMgr,
    private router: AppRouter,
    private injector: Injector,
    private translateService: TranslateService,
    private APIFactory: APIFactory,
    private dispatcher: APIDispatch,
    private checkPermissionService: CheckPermissionService,
    private menuService: MenuService,
    private routeReuseStrategy: RouteReuseStrategy,
    @Inject(ConfigToken) private APP_CONFIG: any
  ) { }

  ngOnInit() {
    this.init();

    this.menuService.getLastUpdateTime()
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(time => {
        console.log("menu lastUpdateTime update:", time);
        this.updateTime = !!time ? format(time, "dd MMM yyyy HH:mm") : null;
      })

    this.translateService.getUpdateSubject()
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(() => {
        this.loadMenu();
      });

  }

  async init() {
    await this.loadMenu();
    let current = this.leftMenu.filter(x => this.isRouteActive(x.getLink()));
    if (current.length > 0) {
      await this.menuService.refreshUpdateTime(current[0].getCode());
    }
  }

  ngOnDestroy() {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  async menuClick(obj, e) {
    console.log("menuClick:", obj);
    if (obj.getCode() === 'BackMainApp') {
      window.location.assign(obj.getLink());
    }
    else {
      this.router.setCurrentMenuObject(obj);
      e.stopPropagation();
      e.preventDefault();
      this.clearHistory();
      this.router.navigate('MenuLoading');
    }
  }
  toggleSettingOpen() {
    this.settingOpen = !this.settingOpen;
    if (this.settingOpen) {
      setTimeout(function () {
        let containerHeight = document.getElementsByClassName('ui-row')[0].clientHeight - document.getElementsByClassName('ui-main-logo')[0].clientHeight - document.getElementsByClassName('setting-inner-list-wrapper')[0].clientHeight;
        let menuScrollContainer = document.getElementsByClassName('ui-main-menu');
        let menuScrollContent = document.getElementsByClassName('main-menu-ul');
        let menuScrollContentHeight = menuScrollContent[0].clientHeight;
        console.log(menuScrollContentHeight - containerHeight);
        menuScrollContainer[0].scrollTo({
          top: menuScrollContentHeight - containerHeight,
          behavior: 'smooth'
        })
        console.log(menuScrollContainer[0].scrollTop);
      }, 700)
    }
  }
  checkSettingOpenOrNot() {
    return this.settingOpen ? 'shown' : 'hidden';
  }

  clearHistory() {
    this.menuMoreOpen = false;
    this.menuHeaderOpen = false;
    // (<CustomReuseStrategy>this.routeReuse).clearHandlers();
    if ((<CustomRouterReuseStrategy>this.routeReuseStrategy).deleteRouteSnapshot) {
      (<CustomRouterReuseStrategy>this.routeReuseStrategy).deleteRouteSnapshot();
    }
  }


  onRouterOutletActivate(component: any) {
    // console.warn('onRouterOutletActivate', component);
    // console.warn('mobileContentHeight', this.mobileContentHeight);

    // ui-main-left setTimeout 100ms to calculate height
    var _this = this;
    setTimeout(function () {
      // console.warn('main mobileContentHeight', _this.mobileContentHeight);

      component.parentHeight = _this.mobileContentHeight;
    }, 150);
  }

  private async loadMenu() {
    let getMenuAPI = this.APIFactory.getAPI("getMenu");
    let resp = await this.dispatcher.dispatch(getMenuAPI).toPromise();
    let filtered = this.checkPermissionService.authMenu(resp.map(x => x.pageID));
    this.leftMenu = resp
      .filter(x => filtered.indexOf(x.pageID) > -1)
      .map(x => new MainMenuOption(this.getUrl(x.pageID), this.translateService.translate(x.code), x.pageID, x.icon))

    this.quickMenu = resp
      .filter(x => x.quickMenu)
      .filter(x => filtered.indexOf(x.pageID) > -1)
      .map(x => new MainMenuOption(this.getUrl(x.pageID), this.translateService.translate(x.code), x.pageID, x.icon))

    if (this.APP_CONFIG.APP_MODE === APPMODE.Integration) {
      let backOption = new MainMenuOption(this.APP_CONFIG.INTEGRATION_BACK_URL, "Back", "BackMainApp", "nav-icon-home");
      this.quickMenu.unshift(backOption);
      this.leftMenu.push(backOption);
    }

    console.log("resp:", resp);
    console.log("filtered:", filtered);
    console.log('leftMenu:', this.leftMenu);
    console.log("quickMenu:", this.quickMenu);
  }


  public logoutHandler(e) {
    e.preventDefault();
    this.clearHistory();
    this.loginMgr.logout();
  }

  public getUrl(func: string) {
    return this.router.getUrl(func);
  }

  public isRouteActive(link: string): boolean {
    if (!this.baseRouter)
      this.baseRouter = this.injector.get(Router);
    return this.baseRouter.isActive(link, false);
  }

}
