import { Injectable, ErrorHandler } from '@angular/core';
import { Observable, from, Subject, BehaviorSubject } from 'rxjs';
import { APIFactory, APIDispatch, APPError } from '@allianzSND/core';
import { tap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class MenuService {

  private _lastUpdateTime: Date = null;
  private _lastUpdateTimeSubject: Subject<Date> = new BehaviorSubject(this._lastUpdateTime);

  constructor(
    private APIFactory: APIFactory,
    private dispatcher: APIDispatch,
    private errorHandler: ErrorHandler
  ) { }

  public async refreshUpdateTime(func: string) {
    let time = await this._getLastUpdateTimeFromDB(func);
    this._lastUpdateTime = time;
    this._lastUpdateTimeSubject.next(this._lastUpdateTime);
  }

  public getLastUpdateTime() {
    return this._lastUpdateTimeSubject.asObservable();
  }

  private async _getLastUpdateTimeFromDB(func: string): Promise<Date> {
    try {
      func = this.convertPageIDtoFunc(func);
      let updateTimeAPI = this.APIFactory.getAPI("getUpdateTimeList");
      let resp = await this.dispatcher.dispatch(updateTimeAPI).toPromise();
      console.log("getUpdateTimeList resp:", resp);
      let filtered = resp.Body.filter(x => x.MainFunc === func);
      return filtered.length > 0 ? new Date(filtered[0].BackendTime) : null;
    } catch (error) {
      this.errorHandler.handleError(new APPError("F00900", error.message));
      return null;
    }
  }

  private convertPageIDtoFunc(pageID: string): string {
    ["Dashboard", "Customers", "Calendar", "Goal", "Progress"];
    let map = {
      "Dashboard": "Homepage",
      "Customers": "Customer",
      "Goal": "Goal_Setting",
      "Calendar": "Calendar",
      "Progress": "Progress"
    }
    return map[pageID] || '';
  }


}
